import React, {useState, useEffect} from 'react'

function Cell(props){
    const {date, color, day, weightMap, setWeightMap} = props;
    const [currentValue, changeCurrentWeight] = useState(weightMap.has(date.getTime()) ? weightMap.get(date.getTime()): "");
    function weightChange(event){
        changeCurrentWeight(event.target.value);
    }
    useEffect(
        () => {
            changeCurrentWeight(weightMap.has(date.getTime()) ? weightMap.get(date.getTime()): "");
        },
        [date.getTime()],
    );
    useEffect(
        () => {
            if(!(currentValue === ""))  setWeightMap(new Map([...new Map(weightMap.set(date.getTime(), currentValue))].sort()));
            if(currentValue === ""){
                weightMap.delete(date.getTime())
                setWeightMap(new Map(weightMap))
            } 
        },[currentValue]
    );
    return(
        <td>
            <div className="Cell" style={{backgroundColor:color}}>
                <div className="cell-day">{day}</div>
                <input className="input-text" type="text" value={currentValue} onChange={weightChange}style={{background:'transparent', border:'transparent'}}></input>
            </div>
        </td>
    )
}

export default Cell;